
import {useDisplay} from "vuetify";
import {computed} from "vue";
import {defineComponent} from "vue";
//       
export type Get = {
  dirId: string,
  sort: string | null
}
export default defineComponent({
  //  
  props: ['currentDir'],
  data(){
    return {
      // 
      loading: false,
      //  
      item: '',
      // options
      items: [
        {name: ' ', type: 'name'},
        {name: ' ', type: 'type'},
        {name: ' ', type: 'date'}
      ]
    }
  },
  watch: {
    //        
    async item(value) {
      try {
        //  
        const payload: Get = {
          // 
          dirId: this.currentDir,
          //  
          sort: value.type
        }
        this.loading = true
        await this.$store.dispatch('files/get', payload)
        this.loading = false
      } catch (err) {
        console.log(err)
      }
    }
  },
  setup() {
    //   
    const {name} = useDisplay()
    //     
    const cols = computed(() => {
      switch (name.value) {
        case 'xs':
          return 12
        case 'sm':
          return 6
        case 'md':
          return 3
        case 'lg':
          return 3
        case 'xl':
          return 3
        case 'xxl':
          return 3
      }
      return undefined
    })
    return {cols}
  }
})
